<template id="roof">
  <div>
    <h1>Risk Grading System</h1>
    <input type="text" v-model="msg">
    <button @click="showChildComponentData">评估</button>
    <div v-show="isshow">
      <p v-bind="item in select">{{item}}</p>
    </div>
    <P> 已收录 {{ number }} 家机构 </P>
    <router-link to="/About">About</router-link>
  </div>
</template>

<script type="text/javascript">
import jQuery from 'jquery'
var getname = []
export default {
  data () {
    return {
      isshow: true,
      msg: '',
      films: [],
      select: [],
      number: '19982'
    }
  },
  mounted () {
    this.realname()
  },
  methods: {
    realname () {
      var that = this
      var key = that.msg
      jQuery.ajax({
        type: 'post',
        async: false,
        url: 'servlet/helloWorld',
        data: {keyword: key},
        dataType: 'json',
        success: function (result) {
          if (result) {
            for (var i = 0; i < result.length; i++) {
              for (var k = 0; k < result.length - 1; k++) {
                if (result[i].name !== result[k].name) {
                  getname.push(result[i].name)
                }
              }
            }
          }
        }
      })
    },
    showChildComponentData: function () {
      this.$store.state.keyword = this.msg
      this.$router.push({path: '/Result'})
    }
  },
  watch: {
    msg: function (val, oldVal) {
      if (val.length === 0) {
        this.isshow = false
      } else {
        this.isshow = true
        var films = getname
        this.films.forEach((item, index) => {
          if (item.indexOf(val) >= 0) {
            films.unshift(item)
          }
        })
        this.select = films
      }
    }
  }
}
</script>

<style scoped>
ul{
  border:1px solid red;
} 
li{
  height:40px;
  line-height: 40px;
  border-bottom: 1px solid #ddd;
}
.bindingbtn input{
  border:1px solid #333;
  height:44px;
  line-height:44px;
}
</style>



