<template id="view3">
  <div id="echartContainer2" style="width:800px; height:800px">
  </div>
</template>

<script>
import jQuery from 'jquery'
export default {
  name: 'bar-chart',
  data () {
    return {
      title: {
        x: 'center',
        text: '舆情趋势'
      },
      tooltip: {},
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: {
        type: 'value'
      },
      yAxis: {
        type: 'category',
        axisTick: {show: false},
        data: ['2016/4', '2016/8', '2016/12', '2017/4', '2017/8', '2017/12']
      },
      series: [{
        name: '等级',
        type: 'bar',
        label: {
          noraml: {
            show: true,
            position: 'inside'
          }
        },
        data: [1, 2, 3]
      }]
    }
  },
  mounted () {
    this.$nextTick(function () {
      this.drawBarChart()
    })
  },
  methods: {
    drawBarChart () {
      var that = this
      var key = that.$store.state.keyword
      var myChart = that.$echarts.init(document.getElementById('echartContainer2'), 'infographic')
      myChart.showLoading()
      var getoptiondata = []
      var getname = []
      var change = [0, 0, 0, 0, 0, 0]
      var wnum = 0
      var bnum = 0
      var fnum = 0
      var gra = 50
      jQuery.ajax({
        type: 'post',
        async: false,
        url: 'servlet/helloWorld',
        data: {keyword: key},
        dataType: 'json',
        success: function (result) {
          if (result) {
            for (var i = 0; i < result.length; i++) {
              for (var k = 0; k < result.length - 1; k++) {
                if (result[i].name !== result[k].name) {
                  getname.push(result[i].name)
                }
              }
              var geturl = []
              var gettitle = []
              var getdigest = []
              geturl.push(result[i].url)
              gettitle.push(result[i].title)
              getdigest.push(result[i].digest)
              let tr = document.createElement('tr')
              tr.innerHTML = '<a href = "' + geturl + '">' + gettitle + '</a>'
              let tr2 = document.createElement('tr')
              tr.innerHTML = '<a>' + getdigest + '</a>'
              document.getElementById('echartContainer2').appendChild(tr)
              document.getElementById('echartContainer2').appendChild(tr2)
              if (result[i].time < 1460049440000) {
                if (result[i].eventLevel === '正向') {
                  wnum++
                  gra = gra - 10
                  change[0]++
                } else {
                  if (result[i].eventLevel === '中性') {
                    bnum++
                  } else {
                    gra = gra + 10
                    fnum++
                    change[0]--
                  }
                }
              } else {
                if (result[i].time < 1470049440000) {
                  if (result[i].eventLevel === '正向') {
                    wnum++
                    gra = gra - 10
                    change[1]++
                  } else {
                    if (result[i].eventLevel === '中性') {
                      bnum++
                    } else {
                      fnum++
                      gra = gra + 10
                      change[1]--
                    }
                  }
                } else {
                  if (result[i].time < 1480049440000) {
                    if (result[i].eventLevel === '正向') {
                      wnum++
                      gra = gra - 10
                      change[2]++
                    } else {
                      if (result[i].eventLevel === '中性') {
                        bnum++
                      } else {
                        fnum++
                        gra = gra + 10
                        change[1]--
                      }
                    }
                  } else {
                    if (result[i].time < 1490049440000) {
                      if (result[i].eventLevel === '正向') {
                        wnum++
                        gra = gra - 10
                        change[3]++
                      } else {
                        if (result[i].eventLevel === '中性') {
                          bnum++
                        } else {
                          fnum++
                          gra = gra + 10
                          change[3]--
                        }
                      }
                    } else {
                      if (result[i].time < 1500049440000) {
                        if (result[i].eventLevel === '正向') {
                          wnum++
                          gra = gra - 10
                          change[4]++
                        } else {
                          if (result[i].eventLevel === '中性') {
                            bnum++
                          } else {
                            fnum++
                            gra = gra + 10
                            change[4]--
                          }
                        }
                      } else {
                        if (result[i].eventLevel === '正向') {
                          wnum++
                          gra = gra - 10
                          change[5]++
                        } else {
                          if (result[i].eventLevel === '中性') {
                            bnum++
                          } else {
                            fnum++
                            gra = gra + 10
                            change[5]--
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
            getoptiondata.push({value: wnum, name: '正向'})
            getoptiondata.push({value: bnum, name: '中性'})
            getoptiondata.push({value: fnum, name: '负向'})
            this.$store.state.grade = this.gra
            this.$store.state.getoptiondate = this.getoptiondate
            this.$store.state.change = this.change
          }
        }
      })
      setTimeout(() => {
        myChart.setOption({
          title: {
            x: 'center',
            text: '舆情趋势'
          },
          tooltip: {},
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'value'
          },
          yAxis: {
            type: 'category',
            axisTick: {show: false},
            data: ['2016/4', '2016/8', '2016/12', '2017/4', '2017/8', '2017/12']
          },
          series: [{
            name: '等级',
            type: 'bar',
            label: {
              noraml: {
                show: true,
                position: 'inside'
              }
            },
            data: this.$store.state.change
          }]
        })
        myChart.hideLoading()
      }, 2000)
    }
  }
}
</script>

<style>
</style>


