<template>
  <div id="view1">
  <!--创建一个echarts的容器-->
    <div id="pieContainer" style="width:700px; height:700px"></div>
  </div>
</template>

<script>
var echarts = require('echarts')
export default {
  name: '',
  data () {
    return {
    }
  },
  mounted () {
    let myChart = echarts.init(document.getElementById('pieContainer'))
    myChart.setOption({
      itemStyle: {
        normal: {
          shadowBlur: 100,
          shadowOffsetX: 0,
          shadowOffsetY: 0,
          shadowColor: 'rgba(0, 0, 0, 0.5)'
        }
      },
      backgroundColor: '#2c343c',
      label: {
        normal: {
          fontStyle: 'italic'
        }
      },
      labelLine: {
        normal: {
          lineStyle: {
            color: 'rgba(255, 255, 255, 0.3)'
          }
        }
      },
      series: [{
        name: '总体情况',
        type: 'pie',
        radius: [0, 130],
        label: {
          normal: {
            position: 'inner'
          }
        },
        labelLine: {
          normal: {
            show: false
          }
        },
        data: this.$store.state.optiondate
      },
      {
        name: '总体情况',
        type: 'pie',
        selectedMode: 'single',
        radius: [150, 220],
        label: {
          normal: {
            formatter: '{a|{a}}{abg|}\n{hr|}\n  {b|{b}：}{c}  {per|{d}%}  ',
            backgroundColor: '#eee',
            borderColor: '#aaa',
            borderWidth: 1,
            borderRadius: 4,
            rich: {
              a: {
                color: '#999',
                lineHeight: 22,
                align: 'center'
              },
              hr: {
                borderColor: '#aaa',
                width: '100%',
                borderWidth: 0.5,
                height: 0
              },
              b: {
                fontSize: 16,
                lineHeight: 33
              },
              per: {
                color: '#eee',
                backgroundColor: '#334455',
                padding: [2, 4],
                borderRadius: 2
              }
            }
          }
        },
        labelLine: {
          normal: {
            show: false
          }
        },
        data: [
          {value: 335, name: '食品安全质检'},
          {value: 310, name: '诉讼案件'},
          {value: 234, name: '违法行为'},
          {value: 135, name: '金融违规'},
          {value: 147, name: '媒体曝光'},
          {value: 102, name: '其他'}
        ]
      }
      ]
    }
    )
  }
}
</script>
        
<style scoped>  
</style>




